import React, {MouseEvent, useEffect, useState} from 'react'
import styles from './TableContainers.module.scss'
import './TableContainersDraggable.scss'
import {
    ContainerType,
    DaysListType,
    GroupedContainersListType,
    TableContentParamsType
} from '../../../../00_redux/types';
import Draggable, {DraggableData, DraggableEvent} from 'react-draggable';
import {useDispatch, useSelector} from 'react-redux';
import {AppRootStateType} from '../../../../../01_redux/store';
import {loadTableContentParamsAC} from '../../../../00_redux/actions';
import {BvLinearEvent} from './01_bvLinearEvent';
import {moveContainerToNewPositionTC} from '../../../../00_redux/thunks';
import {ModalWindow} from '../../../../../00_common/02_components/01_ModalWindow/ModalWindow';


export const TableContainers: React.FC = () => {
    const dispatch = useDispatch()
    const {
        viewWidth,
        contentWidth,
        scrollLeft,
        tableCellXpx,
        tableCellYpx,
    } = useSelector<AppRootStateType, TableContentParamsType>(state => state.scheduler.tableContentParams)
    const viewTimeLineSize = useSelector<AppRootStateType, number>(state => state.scheduler.viewTimeLineSize)
    const daysList = useSelector<AppRootStateType, DaysListType>(state => state.scheduler.daysList)
    const containersList = useSelector<AppRootStateType, ContainerType>(state => state.scheduler.containersList)
    const groupedContainersList = useSelector<AppRootStateType, GroupedContainersListType>(state => state.scheduler.groupedContainersList)


    // ==================================================================================
    type InitDataType = {
        [id: string]: {
            xy: {
                x: number,
                y: number,
            },
            width: number,
        }
    }

    const [initData, setInitData] = useState<InitDataType>({})

    useEffect(() => {
        const initDataObj: InitDataType = {}
        daysList.forEach((date, dateIndex) => {
            groupedContainersList[date]?.forEach(array => {

                const width = tableCellXpx / array.length
                array.forEach((id, pos) => {
                    const container = containersList[id]

                    initDataObj[id] = {
                        xy: {
                            x: tableCellXpx * dateIndex + pos * width,
                            y: container.startTime * tableCellYpx / viewTimeLineSize,
                        },
                        width: width,
                    }
                })

            })
        })
        // console.log('initData (useEffect)', initDataObj)

        setInitData(initDataObj)
    }, [daysList, groupedContainersList, tableCellXpx, tableCellYpx, viewTimeLineSize])
    // ==================================================================================


    // ==================================================================================
    const onStartHandler = (e: DraggableEvent, data: DraggableData, id: string) => {
        setInitData(state => {
            return {
                ...state,
                [id]: {
                    xy: {
                        x: Math.trunc(data.x / tableCellXpx) * tableCellXpx,
                        y: Math.trunc(data.y / tableCellYpx) * tableCellYpx,
                    },
                    width: tableCellXpx,
                }
            }
        })
    }

    const onDragHandler = (e: DraggableEvent, data: DraggableData) => {
        // console.log(
        //     'LastX:', data.lastX,
        //     'deltaX:', data.deltaX,
        //     'x:', data.x,
        //     'viewWidth:', viewWidth//     'scrollLeft:', scrollLeft,
        // )

        if (viewWidth + scrollLeft < contentWidth && data.x >= viewWidth + scrollLeft) {
            dispatch(loadTableContentParamsAC({
                scrollLeft: Math.trunc((viewWidth + scrollLeft + tableCellXpx) / tableCellXpx) * tableCellXpx - viewWidth,
            } as TableContentParamsType))
        }

        if (scrollLeft > 0 && data.x < scrollLeft) {
            dispatch(loadTableContentParamsAC({
                scrollLeft: Math.trunc((scrollLeft - tableCellXpx) / tableCellXpx) * tableCellXpx,
            } as TableContentParamsType))
        }
    }

    const onStopHandler = (e: DraggableEvent, data: DraggableData, containerId: string) => {
        // console.log(
        //     'x=',data.x,
        //     'LastX:', data.lastX,
        //     'y=',data.y,
        //     'LastY:', data.lastY,
        // )
        // dispatch(moveContainerToNewPositionAC(containerId, Math.trunc(data.lastX / tableCellXpx), Math.trunc((data.lastY / tableCellYpx) * viewTimeLineSize)))
        dispatch(moveContainerToNewPositionTC(containerId, daysList[Math.trunc(data.lastX / tableCellXpx)], Math.trunc((data.lastY / tableCellYpx) * viewTimeLineSize)))
    }
    // ==================================================================================


    // ==================================================================================
    const [modalActive, setModalActive] = useState(false)

    const onContainerContextMenuHandler = (e: MouseEvent<HTMLDivElement>, id: string) => {
        e.preventDefault()

        setModalActive(true)


    }
    // ==================================================================================


    const tableContainers: Array<JSX.Element> = []
    daysList.forEach((date, dateIndex) => {
        // groupedContainersList[date]?.forEach(array => {
        if (groupedContainersList[date]) {
            groupedContainersList[date].forEach(array => {

                array.forEach((id, pos) => {

                    // console.log('initData (draw)', initData)

                    const container = containersList[id]
                    tableContainers.push(
                        <Draggable key={id}
                                   bounds={'parent'}
                                   onStart={(e, data) => onStartHandler(e, data, id)}
                                   onDrag={onDragHandler}
                                   onStop={(e, data) => onStopHandler(e, data, id)}
                                   grid={[tableCellXpx, tableCellYpx]}
                                   position={initData[id] ? initData[id].xy : {x: -10, y: -10}}
                        >

                            <div className={styles.main}
                                 onContextMenu={(e) => onContainerContextMenuHandler(e, id)}
                            >
                                <BvLinearEvent id={id}
                                               primaryText={container.primaryText}
                                               secondaryText={container.secondaryText}
                                               tagText={container.tagText}
                                               objectType={container.objectType}
                                               objectEpisodeInfo={container.objectEpisodeInfo}
                                               objectDuration={`${Math.trunc(container.objectDuration / 60)}m`}
                                               height={container.objectDuration * tableCellYpx / (viewTimeLineSize * 60)}
                                               width={initData[id] ? initData[id].width : 0}
                                               color={container.color}
                                />
                            </div>

                        </Draggable>
                    )


                })

            })
        }
    })


    return (
        <>
            {tableContainers}

            {/*<ModalWindow*/}
            {/*    active={modalActive}*/}
            {/*    onModalEntireWindowClick={() => setModalActive(false)}>*/}
            {/*    Test*/}
            {/*</ModalWindow>*/}
        </>
    )
}


