import {SchedulerStateType} from './types';
import {initialState} from './state';
import {Actions, SchedulerActionsType} from './actions';
import moment from 'moment';


export const schedulerReducer = (state: SchedulerStateType = initialState, action: Actions): SchedulerStateType => {
    switch (action.type) {
        case SchedulerActionsType.LOAD_TABLE_CONTENT_PARAMS: {
            return {
                ...state,
                tableContentParams: {
                    ...state.tableContentParams,
                    ...action.params,
                },
            }
        }


        case SchedulerActionsType.SET_DAYS_LIST: {
            return {
                ...state,
                daysList: action.days,
            }
        }


        case SchedulerActionsType.SET_VIEW_TIME: {
            return {
                ...state,
                viewTimeLineSize: action.time,
            }
        }

        case SchedulerActionsType.SET_VIEW_DAYS: {
            return {
                ...state,
                viewDays: action.days,
            }
        }

        case SchedulerActionsType.SET_VIEW_DAYS_OF_WEEK: {
            const newArray = [...state.viewDaysOfWeek]
            if (action.dayOfWeek >= 0 && action.dayOfWeek <= 6) {
                newArray[action.dayOfWeek] = action.value
            }

            return {
                ...state,
                viewDaysOfWeek: newArray,
            }
        }


        case SchedulerActionsType.MOVE_CONTAINER_TO_NEW_POSITION: {
            const container = state.containersList[action.containerId]

            return {
                ...state,
                containersList: {
                    ...state.containersList,
                    [action.containerId]: {
                        ...container,
                        logDate: action.newDate,
                        startTime: action.newTime
                    }
                },
            }
        }


        case SchedulerActionsType.INIT_GROUPED_CONTAINER_LIST: {
            return {
                ...state,
                groupedContainersList: action.groupedContainersList,
            }
        }


        case SchedulerActionsType.SET_VIEW_SIDE_PANEL: {
            return {
                ...state,
                viewSidePanel: action.value,
            }
        }


        case SchedulerActionsType.SET_DRAG_START_SIDE_PANEL: {
            return {
                ...state,
                dragStartSidePanel: action.value,
                draggedContainerId: action.draggedContainerId,
            }
        }


        case SchedulerActionsType.ADD_NEW_CONTAINER: {
            return {
                ...state,
                containersList: {
                    ...state.containersList,
                    [action.containerId + '_' + Date.now()]: {
                        ...action.container,
                        logDate: action.date,
                        startTime: action.time,
                        chanelID: action.chanelID,
                    }
                },
            }
        }


        default:
            return state
    }
}


