import React from 'react'
import styles from './TableBody.module.scss'
import {Columns} from './02_Columns/Columns';
import {TimeLine} from './01_TimeLine/TimeLine';
import {useSelector} from 'react-redux';
import {AppRootStateType} from '../../../../01_redux/store';

export const TableBody: React.FC = () => {
    const viewSidePanel = useSelector<AppRootStateType>(state => state.scheduler.viewSidePanel)

    return (
        // <div className={styles.main}>
        <div className={`${styles.main} ${viewSidePanel ? styles.withSidePanel : styles.withoutSidePanel}`}>
            <TimeLine/>
            <Columns/>
            <TimeLine/>
        </div>
    )
}